const parser: FormulaParser = new FormulaParser({}) as FormulaParser

export function getTestResultFormulaAtInputedText(inputedTextFormula: string) {
    console.log('inputedTextFormula')
    const formulaFunctions = [...formulaMap.keys()] as string[]

    let inputedTextFormulaLocal = inputedTextFormula
    let inputedTextFormulaWithRanges = ''
    
    formulaFunctions.forEach((nameFunc: string) => {
        inputedTextFormulaLocal = inputedTextFormulaLocal.replaceAll(nameFunc, (formulaMap.get(nameFunc) as IFormula).nameEng)
        inputedTextFormulaLocal = inputedTextFormulaLocal.replaceAll(';', ',')
    })

    const idx = inputedTextFormulaLocal.indexOf(":")
    //          

    if (idx !== -1) {
        //    
        inputedTextFormulaLocal = replaceColonWithFields(inputedTextFormulaLocal)
        inputedTextFormulaWithRanges = replaceColonWithFields(inputedTextFormulaLocal)  
    } else {
        inputedTextFormulaWithRanges = inputedTextFormulaLocal
    }
    
    nestedHighHeaderRange.forEach((columnNameRange: EnumColumnNameRange) => {
        if (inputedTextFormulaLocal.includes(columnNameRange)) {
            //  e   ,     
            const prop = propsRangeNameMap.get(columnNameRange)
            const value = instance.getDataAtRowProp(0, prop)
            inputedTextFormulaLocal = inputedTextFormulaLocal.replaceAll(columnNameRange, value )
        }
    })

    let resultFormula: IResultFormula
    try {
        //:      
        let isHaveColumnNameRangeInFormula = false
        let keyColumnNameRangeInFormula = 0

        nestedHighHeaderRange.forEach((columnNameRange: EnumColumnNameRange) => {
            if (inputedTextFormulaWithRanges.includes(columnNameRange)) {
                keyColumnNameRangeInFormula++
            }
        })

        //:      
        if (keyColumnNameRangeInFormula > 0) isHaveColumnNameRangeInFormula = true

        const resultParse = parser.parse(inputedTextFormulaLocal)
        resultFormula = {text: resultParse, error: false, isHaveRangeInFormula: isHaveColumnNameRangeInFormula, typeValue: typeof resultParse}
        //     

        //     ""
        setNewFormulaToState(inputedTextFormula, inputedTextFormulaWithRanges, false, typeof resultParse as EnumTypeField)
    
    } catch (formulaError: FormulaError) {
        setNewFormulaToState(inputedTextFormula, inputedTextFormulaWithRanges, true, typeof 'error' as EnumTypeField)
        resultFormula = {text: formulaError.name, error: true, isHaveRangeInFormula: false, typeValue: typeof formulaError.name}
    }
        
    return  resultFormula
}

function setNewFormulaToState(formulaTextOriginal: string, formulaText: string, error: boolean, typeValue: EnumTypeField) {
    
    if (error === true) {
        //       
        storage.dispatch('clearFakeCurrentFormula')
    } else {
        let formulaTextLocal = formulaText
        
        const depends: string[] = []
        nestedHighHeaderRange.forEach((columnNameRange: EnumColumnNameRange) => {
            if (formulaText.includes(columnNameRange)) {
                const psevdoName = (propsRangeNameMapFull.get(columnNameRange) as IPropsRangeNameMapValue).psevdoName
                formulaTextLocal = formulaTextLocal.replaceAll(columnNameRange, psevdoName)
    
                depends.push((propsRangeNameMapFull.get(columnNameRange) as IPropsRangeNameMapValue).fieldName)
            }
        })

        //     
        const tokensFromformulaTextLocal: string[] = lex(formulaTextLocal).map(token => token.image) as string[];
        
        //    
        propsRangeNameMapFull.forEach((values: IPropsRangeNameMapValue, keys: EnumColumnNameRange) => {
            tokensFromformulaTextLocal.forEach((token: string, idx: number, thisArray: string[]) => {
                if (values.psevdoName === token) {
                    thisArray[idx] = keys
                }
            })
        });

        const newFakeCurrentFormula: IUserFormula = {
            id: String(new Date().getTime()),
            typeValue: typeValue,
            column: 9999,
            depends: depends,
            formulaText: formulaText,
            formulaTextOriginal: formulaTextOriginal,
            fieldName: '9999',
            tokens: tokensFromformulaTextLocal
        }
        
        //   
        storage.dispatch('setFakeCurrentFormula', newFakeCurrentFormula)
    }
}

function replaceColonWithFields(inputedTextFormula: string) {

    let inputedTextFormulaLocal = inputedTextFormula
    const colons = []
    for(let i = 0; i < inputedTextFormulaLocal.length; i++) {
        if (inputedTextFormulaLocal[i] === ':') {
            colons.push(i)
        }
    }

    // 
    const arrayShortRanges: string[] = []

    if (colons.length) {
        const usedIdxMatchFieldToRightArray: number[] = []
        colons.forEach((idColon: number, idx: number) => {
            let leftRange = ''
            let leftIdxRange: number | null = null
            let rightRange = ''
            let rightIdxRange: number | null = null

            
            //      
            nestedHighHeaderRange.forEach((columnNameRange: EnumColumnNameRange) => {
                //   :
                const idxMatchFieldToLeft = inputedTextFormulaLocal.lastIndexOf(columnNameRange, idColon) 

                if (idxMatchFieldToLeft !== -1 && (idxMatchFieldToLeft + columnNameRange.length) === idColon) {
                    //   
                    leftRange = columnNameRange
                    leftIdxRange = idxMatchFieldToLeft
                }

                //   :
                const idxMatchFieldToRight = inputedTextFormulaLocal.indexOf(columnNameRange, idColon)

                if (idxMatchFieldToRight !== -1 && (idxMatchFieldToRight -1) === idColon) {
                    //  
                    usedIdxMatchFieldToRightArray.push(idxMatchFieldToRight)
                    rightRange = columnNameRange
                    rightIdxRange = idxMatchFieldToLeft
                }

            })

            //:      .  - ,      
            const usedIdxMatchFieldToRight: boolean = usedIdxMatchFieldToRightArray.some(r=> r === leftIdxRange)

            if (idx >= 1 && usedIdxMatchFieldToRight === true) {
                //
            } else {
                const resultRange = `${leftRange}:${rightRange}`
                arrayShortRanges.push(resultRange)
            }
            
        })
    }

    if (arrayShortRanges.length) {
        
        arrayShortRanges.forEach((shortRange: string) => {
            const spreadRange = generateSpreadRange(shortRange)
            inputedTextFormulaLocal = inputedTextFormulaLocal.replace(shortRange, spreadRange)
        })
    }
    return inputedTextFormulaLocal
}

function generateSpreadRange(shortRange: string): string {
    const shortRangeLocal = shortRange
    const leftRange: string =  shortRangeLocal.split(':')[0]
    const rightRange: string =  shortRangeLocal.split(':')[1]

    if (nestedHighHeaderRange.some(t => t === leftRange) && nestedHighHeaderRange.some(t => t === rightRange)) {
        const leftRangeIdx: number =  nestedHighHeaderRange.findIndex((field: EnumColumnNameRange) => field === leftRange)
        const rightRangeIdx: number =  nestedHighHeaderRange.findIndex((field: EnumColumnNameRange) => field === rightRange)
    
        const resultSpreadRange = nestedHighHeaderRange.map((field: EnumColumnNameRange, idx: number) => {
            if (idx >= leftRangeIdx && idx <= rightRangeIdx) {
                return field
            }
        }).filter(e => e).join(",")
    
        return resultSpreadRange
    }

    return ''
}

interface IChange {
    changefileldName: EnumFieldName,
    processed: boolean,
    changes: Array<[number, any]> //, 
}

//    setDataAtRowProp
let arrayForSetDataAtRowProp: IChange[] = []

export async function setDataAtRowPropFromContextMenu(columnEditing: number, dataValueLength: number) {

    const fieldName = (fieldsInfoMap.get(columnEditing) as IField).fieldName as EnumFieldName
    const fakeCurrentFormula = storage.getters.fakeCurrentFormula as IUserFormula

    if (fakeCurrentFormula) {
        //  Map     
        const newColumnFormula: IUserFormula = {...fakeCurrentFormula, column: columnEditing, fieldName, id: String(new Date().getTime())} 
        storage.dispatch('setFormulaObject', {key: columnEditing, value: newColumnFormula})
    }

    const formulaObject: IUserFormula | undefined = (storage.getters.formulaObjectByColumn(columnEditing))

    const changes: any[] = []

    if (formulaObject) {
        const tokens = formulaObject.tokens
        
        for (let i = 0; i <= dataValueLength -1; i++) {
            const tokensLal: string[] = [...tokens]

            tokens.forEach((token: any, index: number) => {
            if (nestedHighHeaderRange.includes(token)) {
                const prop: string = propsRangeNameMap.get(token) as string
                const value: any = instance.getDataAtRowProp(i, prop)
                tokensLal[index] = String(value)
            }
            })

            const formulaText: string = tokensLal.join("")
            const resultFormulaValue = parser.parse(formulaText)

            changes.push([i, resultFormulaValue])
        }
    }

    if (changes.length) {

        arrayForSetDataAtRowProp = []
        arrayForSetDataAtRowProp.push({changefileldName: fieldName,processed: true, changes: changes })

        await proseccedDependsByProp(changes, fieldName)

        if (arrayForSetDataAtRowProp.length) {
            arrayForSetDataAtRowProp.forEach(async (change: IChange) => {
                if (change.processed === false) {
                    //  -
                    await proseccedDependsByProp(change.changes, change.changefileldName)
                    const depend = arrayForSetDataAtRowProp.find((change: IChange) => change.changefileldName === change.changefileldName)
    
                    if (depend) depend.processed = true
                }
            })
        }

        setTimeout(() => {
            let resultArray: any[] = []
    
            if (!arrayForSetDataAtRowProp.length) return
    
            arrayForSetDataAtRowProp.forEach((change: IChange) => {
                change.changes.forEach((changeRow: [number, any]) => {
                    resultArray.push([changeRow[0], change.changefileldName, changeRow[1] ])
                })
            })

            instance.setDataAtRowProp(resultArray)
            
            //  
            resultArray = []
            arrayForSetDataAtRowProp = []
        })
    }
}

export async function setDataAtRowProp(changesParam: any[], fileldNameParam: EnumFieldName) {

    await proseccedDependsByProp(changesParam, fileldNameParam)

    if (arrayForSetDataAtRowProp.length) {
        arrayForSetDataAtRowProp.forEach(async (change: IChange) => {
            if (change.processed === false) {
                //  -
                await proseccedDependsByProp(change.changes, change.changefileldName)
                const depend = arrayForSetDataAtRowProp.find((change: IChange) => change.changefileldName === change.changefileldName)

                if (depend) depend.processed = true
            }
        })
    }
    
    setTimeout(() => {
        let resultArray: any[] = []

        if (!arrayForSetDataAtRowProp.length) return

        arrayForSetDataAtRowProp.forEach((change: IChange) => {
            change.changes.forEach((changeRow: [number, any]) => {
                resultArray.push([changeRow[0], change.changefileldName, changeRow[1] ])
            })
        })

        instance.setDataAtRowProp(resultArray)
        
        //  
        resultArray = []
        arrayForSetDataAtRowProp = []
    })
}

export function proseccedDependsByProp(changes: any[], fieldNameParam: EnumFieldName) {
    //console.log('START =-proseccedDependsByProp', fieldNameParam)
    const depends = storage.getters.dependsByField(fieldNameParam) as EnumFieldName[]

    depends.forEach((fileldNameDep: EnumFieldName) => {
        
        const formulaObject: IUserFormula | undefined = (Array.from(storage.getters.formulasList.values()) as IUserFormula[]).find((formula: IUserFormula)=> formula.fieldName === fileldNameDep)
        let tokensLal: string[] = []

        if (formulaObject) {
            const tokens = formulaObject.tokens
            tokensLal = [...tokens]

            changes.forEach(async (change: any[]) => {
                
                const row = change[0] as number

                await new Promise((resolve) => {
                    setTimeout(() => {

                        //       
                        tokens.forEach((token: any, index: number) => {
                            if (nestedHighHeaderRange.includes(token)) {
                                const prop = propsRangeNameMap.get(token)
                                // : value         
                                const depend = arrayForSetDataAtRowProp.find((change: IChange) => change.changefileldName === prop)

                                //   
                                let value: any
                                if (depend) {
                                    //       
                                    value = (depend.changes.find((changeRow: [number, any]) => changeRow[0] === row) as any[])[1]
                                } else {
                                    //     
                                    value = instance.getDataAtRowProp(row, prop)
                                }

                                tokensLal[index] = String(value)
                                resolve(tokensLal);
                            }
                        })
                    })
                })

                const formulaText: string = tokensLal.join("")

                let resultFormula: any
                try {
                    resultFormula = await parser.parse(formulaText)
    
                    const depend = arrayForSetDataAtRowProp.find((change: IChange) => change.changefileldName === fileldNameDep)
                    if (depend) {
                        const changesDepend: number = depend.changes.findIndex((change: [number, any]) => change[0] === row)
                        if (changesDepend !== -1) {
                            depend.changes[changesDepend] = [row, resultFormula]
                        } else {
                            
                            depend.changes.push([row, resultFormula])
                            //console.log('push to  depend.changes', [[row, resultFormula]], fieldNameParam)
                        }
                    } else {
                        arrayForSetDataAtRowProp.push({changefileldName: fileldNameDep,processed: false, changes: [[row, resultFormula]] })
                        //console.log('push to arrayForSetDataAtRowProp', [[row, resultFormula]], fieldNameParam)
                    }

                } catch (formulaError: FormulaError) {
                    throw new Error(formulaError.name)
                }
            })
        }
    })
    //console.log('END =-proseccedDependsByProp', fieldNameParam)
}

export async function setDataAtRowPropTest(changes: any[], fieldNameParam: EnumFieldName) {
    const depends = storage.getters.dependsByField(fieldNameParam) as EnumFieldName[]
    const changesForSetArray: any[] = []
    console.log('depends', depends)
    
    depends.forEach((fileldName: EnumFieldName) => {
        
        const formulaObject: IUserFormula | undefined = (Array.from(storage.getters.formulasList.values()) as IUserFormula[]).find((formula: IUserFormula)=> formula.fieldName === fileldName)
        let tokensLal: string[] = []
        //console.log('formulaObject', formulaObject)
        if (formulaObject) {
            //  
            const tokens = formulaObject.tokens
            tokensLal = [...tokens]
            //console.log('changes---setDataAtRowProp',changes)
            changes.forEach(async (change: any[]) => {
                
                const row = change[0] as EnumFieldName

                await new Promise((resolve) => {
                    setTimeout(() => {
                        tokens.forEach((token: any, index: number) => {
                            if (nestedHighHeaderRange.includes(token)) {
                                const prop = propsRangeNameMap.get(token)
                                //   
                                const value = instance.getDataAtRowProp(row, prop)
                                tokensLal[index] = String(value)
                                resolve(tokensLal);
                            }
                        })
                    })
                })

                const formulaText: string = tokensLal.join("")

                let resultFormula: any
                try {
                    resultFormula = parser.parse(formulaText)
                    changesForSetArray.push([row, fileldName, resultFormula])
                } catch (formulaError: FormulaError) {
                    throw new Error(formulaError.name)
                }
            })
        }
    })

    //
    setTimeout(() => {
        if (changesForSetArray.length) {
            console.log('changesForSet', changesForSetArray)
            instance.setDataAtRowProp(changesForSetArray)
        }
    });
}